import React, { useState } from "react";
import { Row, Col } from "antd";
import "bootstrap/dist/css/bootstrap.css";
import {
  Button,
  Modal,
  ModalTitle,
  ModalBody,
  ModalHeader,
  ModalFooter,
  FormGroup,
  Input,
  Label,
} from "reactstrap";

import "./form-users.scss";

export default function FormUsers() {
  const datos = [
    { id: 1, nombre: "Roman", minutos: 250 },
    { id: 2, nombre: "Martin", minutos: 360 },
    { id: 3, nombre: "Schiavi", minutos: 130 },
    { id: 4, nombre: "Guillermo", minutos: 210 },
  ];

  const [data, setData] = useState(datos);
  const [modalEditar, setModalEditar] = useState(false);
  const [modalEliminar, setModalEliminar] = useState(false);

  const [elementoSeleccionado, setElementoSeleccionado] = useState({
    id: "",
    nombre: "",
    minutos: "",
  });

  const seleccionarNombre = (elemento, caso) => {
    setElementoSeleccionado(elemento);
    caso === "Editar" ? setModalEditar(true) : setModalEliminar(true);
  };

  //Obtener lo que escribe en los input
  const handleChange = (e) => {
    console.log(e.target);
    const { name, value } = e.target;
    setElementoSeleccionado((prevState) => ({
      ...prevState,
      [name]: value,
    }));
    console.log(elementoSeleccionado);
  };

  //Editar Informacion
  const Editar = () => {
    var dataNueva = data;
    dataNueva.map((dato) => {
      if (dato.id == elementoSeleccionado.id) {
        dato.nombre = elementoSeleccionado.nombre;
        dato.minutos = elementoSeleccionado.minutos;
      }
    });
    setData(dataNueva);
    setModalEditar(false);
  };

  //Eliminar Informacion
  const Eliminar = () => {
    setData(data.filter((elemento) => elemento.id !== elementoSeleccionado.id));
    setModalEliminar(false);
  };
  return (
    <div className="form-users">
      <table className="table ant-table-bordered">
        <thead>
          <tr>
            <th>ID</th>
            <th>Nombre</th>
            <th>Minutos</th>
            <th>Editar</th>
            <th>Eliminar</th>
          </tr>
        </thead>
        <tbody>
          {data.map((elemento) => (
            <tr key={elemento.id}>
              <td>{elemento.id}</td>
              <td>{elemento.nombre}</td>
              <td>{elemento.minutos}</td>
              <td>
                <button
                  className="btn ant-btn-primary"
                  onClick={() => seleccionarNombre(elemento, "Editar")}
                >
                  Editar
                </button>
              </td>
              <td>
                <button
                  className="btn ant-btn-danger"
                  onClick={() => seleccionarNombre(elemento, "Eliminar")}
                >
                  Eliminar
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <Modal isOpen={modalEditar}>
        <ModalHeader>Ventana Modal</ModalHeader>
        <ModalBody>
          <FormGroup>
            <label htmlFor="id">ID</label>
            <input
              className="form-control"
              type="text"
              name="id"
              defaultValue={elementoSeleccionado && elementoSeleccionado.id}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <label htmlFor="nombre">Nombre</label>
            <input
              className="form-control"
              type="text"
              name="nombre"
              defaultValue={elementoSeleccionado && elementoSeleccionado.nombre}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <label htmlFor="minutos">Minutos</label>
            <input
              className="form-control"
              type="text"
              name="minutos"
              defaultValue={
                elementoSeleccionado && elementoSeleccionado.minutos
              }
              onChange={handleChange}
            />
          </FormGroup>
        </ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={() => setModalEditar(false)}>
            Cerrar
          </Button>
          <Button color="primary" onClick={() => Editar()}>
            Guardar
          </Button>
        </ModalFooter>
      </Modal>

      <Modal isOpen={modalEliminar}>
        <ModalBody>
          Esta seguro que desea eliminar a{" "}
          {elementoSeleccionado && elementoSeleccionado.nombre}?
        </ModalBody>
        <ModalFooter>
          <Button color="danger" onClick={() => Eliminar()}>
            Si
          </Button>
          <Button color="primary" onClick={() => setModalEliminar(false)}>
            No
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
}
